* {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
}

html {
  height: 100%;
}

body {
  height: 100%;
  color: #fff;
}

.App {
  max-width: 100vw;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background: url('https://damion.club/uploads/posts/2022-01/thumbs/1641917097_32-damion-club-p-stilnii-novogodnii-fon-37.jpg');
  background-size: cover;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-bottom: 20px;
  border-bottom: 2px solid #aeb3ad;
  text-align: center;
}

.bodyPage{
  height: auto;
  display: flex;
  flex: 1 1 auto;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-around;
  flex-direction: column;
  text-align: center;

  .wrapper {
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
  }

  button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    min-width: 260px;
    height: 60px;
    background: #cc1c09;
    border: none;
    border-radius: 13px;
    font-size: 20px;
    color: #ffffff;
    cursor: pointer;
  }

  button:disabled {
    background: #ce312048;
    color: rgba(255, 255, 255, 0.7);
  }
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-top: 20px;
  border-top: 2px solid #aeb3ad;
  text-align: center;
}


.cardName {
  display: flex;
  justify-content: center;
  align-items: center;

  h4 {

    div {
      font-size: 28px;
      margin-top: 20px;
    }
  }

}

@media screen and (max-width:968px) {
  .App {
      max-width: 100vw;
      background: url('../public/img/4701046.jpg') !important;
      background-position: top right !important;
      background-size: 100vh !important;
  }

  .header {
    font-size: 2.4vw;
  }
  
  .bodyPage {
    button {
      font-size: 4vw;
      min-width: 40vw;
      height: 11vw;
      border-radius: 7px;
    }
  }

  .cardName {
    padding: 0 20px;
    font-size: 4vw;
    
    h4 {
      

      div {
        font-size: 15vw;
        margin-top: 20px;
      }
    }
  }

  .footer {
    font-size: 4vw;
  }
}